:root{
  --ink:#0f172a;
  --muted:#64748b;
  --accent:#f97316;
  --accent-2:#ef4444;
  --radius-xl:18px;
  --shadow-1:0 10px 28px rgba(2,6,23,.12);
  --shadow-2:0 16px 44px rgba(2,6,23,.16);
}

/* Page grid */
.pd-wrap{ display:grid; grid-template-columns:1fr; gap:2.5rem; }
@media (min-width:1024px){ .pd-wrap{ grid-template-columns:1.05fr 1fr; gap:3rem; } }

/* Media card */
.pd-media{ border-radius:var(--radius-xl); overflow:hidden; background:#fff; box-shadow:var(--shadow-1); }
.pd-media img{ display:block; width:100%; height:440px; object-fit:cover; }

/* Right column card */
.pd-card{
  border-radius:var(--radius-xl);
  background: linear-gradient(180deg, #fff, #f8fafc);
  box-shadow: var(--shadow-1);
  padding:24px;
}
@media (min-width:1024px){ .pd-card{ padding:28px; } }

/* Title + meta */
.pd-title{ margin:0; font-weight:900; letter-spacing:-.02em; }
.pd-meta{ display:flex; flex-wrap:wrap; gap:12px 18px; color:var(--muted); }

/* Badges / pills */
.pill{ display:inline-flex; align-items:center; gap:.4rem; padding:.35rem .7rem; border-radius:9999px; font-weight:800; font-size:.82rem; box-shadow:0 6px 16px rgba(0,0,0,.08); }
.pill-red{ background:#ef4444; color:#fff; }
.badge-green{ background:#dcfce7; color:#166534; border-radius:9999px; padding:.25rem .6rem; font-weight:700; font-size:.82rem; }

/* Price row */
.price-row{ display:flex; align-items:center; gap:12px; }
.price{ font-size: clamp(28px, 2.3vw, 36px); font-weight:900; color:var(--accent); }
.strike{ color:#94a3b8; text-decoration:line-through; font-weight:600; }
.save-pill{ background:#f0fdf4; color:#16a34a; padding:.25rem .6rem; border-radius:9999px; font-weight:700; }

/* Stats */
.stat{ display:flex; gap:.75rem; align-items:flex-start; }
.stat .label{ font-weight:800; color:var(--ink); }
.stat .sub{ color:var(--muted); }

/* Nutrition */
.nutri-card{
  background:#fff;
  border:1px solid rgba(2,6,23,.06);
  border-radius:16px;
  padding:16px 18px;
  box-shadow:0 6px 20px rgba(2,6,23,.06);
}
.nutri-title{ font-weight:800; margin-bottom:.4rem; }
.nutri-grid{ display:grid; grid-template-columns:repeat(2,minmax(0,1fr)); gap:.5rem .75rem; }

/* Actions */
.actions{ display:flex; gap:12px; align-items:center; }
@media (min-width:1024px){ .actions{ position:sticky; bottom:18px; padding-top:8px; } }

.btn-primary{
  display:inline-flex; align-items:center; justify-content:center; gap:.5rem;
  padding:14px 18px; border-radius:12px; font-weight:800;
  color:#000000; border:none; cursor:pointer;
  background: linear-gradient(90deg, var(--accent), var(--accent-2));
  box-shadow:0 10px 22px rgba(249,115,22,.22);
  transition: transform .12s ease, filter .15s ease, box-shadow .15s ease;
}
.btn-primary:hover{ transform:translateY(-1px); filter:brightness(1.05); box-shadow:var(--shadow-2); }
.btn-primary:disabled{ opacity:.55; cursor:not-allowed; box-shadow:none; filter:none; }

.wish-btn{ transition:border-color .15s ease, background .15s ease, color .15s ease; }

/* Reviews block (base) */
.reviews{
  border-radius:16px; background:#fff;
  border:1px solid rgba(2,6,23,.06);
  box-shadow:0 8px 28px rgba(2,6,23,.06);
}

/* Media hover + subtle motion */
.pd-media:hover img {
  transform: scale(1.02);
  transition: transform .3s ease;
}
.pd-media {
  transition: box-shadow .3s ease;
}
.pd-media:hover {
  box-shadow: 0 20px 40px rgba(0,0,0,.15);
}

/* Fancy sheen on primary button */
.btn-primary { position: relative; overflow: hidden; }
.btn-primary::after {
  content:"";
  position:absolute; top:0; left:-75%;
  width:50%; height:100%;
  background:rgba(255,255,255,.25);
  transform:skewX(-20deg);
  transition: left .5s;
}
.btn-primary:hover::after { left:125%; }

/* Enter animation */
.pd-card, .pd-media { opacity:0; transform:translateY(10px); animation: fadeUp .4s ease forwards; }
@keyframes fadeUp { to {opacity:1; transform:translateY(0);} }

/* Reviews gradient background (kept) */
.reviews { border: 1px solid rgba(0,0,0,.06); background: linear-gradient(180deg,#fff,#f9fafb); }

/* ===================== NEW: Review form polish ===================== */

/* Wrapper for the add-review form (optional—your JSX already has Tailwind) */
.review-form { border:1px solid rgba(2,6,23,.06); border-radius:12px; background:#fff; }

/* Interactive stars in the picker */
.star-interactive{
  cursor:pointer;
  transition: transform .12s ease, filter .12s ease;
}
.star-interactive:hover{ transform: scale(1.05); }
.star-interactive:focus{ outline:2px solid #2563eb; outline-offset:2px; border-radius:4px; }

/* Small stars in the review list */
.star-compact{ vertical-align:middle; }

/* Textarea smoothing if you ever use a class (Tailwind already covers) */
.review-textarea{
  width:100%;
  border:1px solid rgba(2,6,23,.12);
  border-radius:10px;
  padding:10px 12px;
  transition:border-color .15s ease, box-shadow .15s ease;
}
.review-textarea:focus{
  outline:none;
  border-color:#2563eb;
  box-shadow:0 0 0 3px rgba(37,99,235,.15);
}
