/* ===== Custom tweaks for back-button, rating header & submit ===== */

/* Back to Menu / Back to Home */
.back-home-btn {
  display:inline-block;
  background:linear-gradient(90deg,#f97316,#ef4444);
  color:#fff;
  font-weight:600;
  padding:8px 16px;
  border-radius:8px;
  text-decoration:none;
  transition:background .2s ease, transform .2s ease;
}
.back-home-btn:hover {
  background:linear-gradient(90deg,#ef4444,#f97316);
  transform:translateY(-1px);
}
.back-home-btn:active {
  transform:translateY(0);
}

/* “Add your rating” heading */
.add-rating-title {
  font-size:1.1rem;
  font-weight:700;
  color:#0f172a;
  margin-bottom:.75rem;
  display:block;
}

/* Submit Review button */
.submit-review-btn {
  display:inline-block;
  background:#2563eb;
  color:#fff;
  font-weight:700;
  padding:10px 20px;
  border-radius:8px;
  border:none;
  cursor:pointer;
  transition:background .2s ease, transform .2s ease;
}
.submit-review-btn:hover {
  background:#1d4ed8;
  transform:translateY(-1px);
}
.submit-review-btn:active {
  transform:translateY(0);
}
.submit-review-btn:disabled {
  opacity:.6;
  cursor:not-allowed;
}
